import React from 'react';
import NotFoundImage from '../img/404-phanorcoll.png';
import Link from 'gatsby-link';

const NotFoundPage = () => (
  <div className="pc-main-wrapper">
    <div className="notfound-wrapper">
      <img src={NotFoundImage} alt="Phanor Coll fullstack developer" />
      <p>
        something went seriously wrong <i className="fa fa-chain-broken" aria-hidden="true"></i>
      </p>
      <div className="back-home">
        <Link to="/"><i className="fa fa-arrow-circle-left" aria-hidden="true"></i>
        Go back home</Link>
      </div>
    </div>
  </div>
)

export default NotFoundPage



